@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
* {
    margin: 0;
    padding: 0;
    font-family: 'Cabin', sans-serif;
    /* letter-spacing: 1px; */
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
}

h1 {
    letter-spacing: 2px;
}
header {
  position: relative;
  height: 100vh;
  width: 100%;
  text-align: center;
  overflow: hidden;
}

.computer-img {
  height: 70%;
  opacity: 0;
  transform: scale(0);
}

header .banner {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-wrap {
  width: 100%;
  letter-spacing: 2.5px; 
  z-index: 100;
  position: fixed;
  transition: 0.5s ease-out;
}

.nav-wrap.active {
  background: #007f68;
  transition: 0.5s ease-out;
}

.nav-wrap.active .nav-link {
  color: #fff;
}

.nav-wrap.active .nav-link:hover {
  color: #f7931e;
}

.nav-menu {
  min-height: 48px;
  width: auto;
  text-align: center;
}

.nav-menu li {
  position: relative;
  list-style: none;
  height: 48px;
  display: inline-block;
}

.nav-link {
  display: inline-block;
  font-size: 16px;
  padding: 8px 13px;
  line-height: 32px;
  cursor: pointer;
  color: #007f68;
  transition: 0.2s;
}

.nav-link:hover {
  color: #f7931e;
  transition: 0.2s;
}

.menu-icon {
  display: none;
}

.icon1 {
  color: #f7931e;
  font-size: 50px;
  position: absolute;
  top: 20%;
  right: 30%;
  transform: scale(0);
  opacity: 0;
}

.icon2 {
  display: flex;
  color: #00A99D;
  font-size: 50px;
  position: absolute;
  top: 75%;
  right: 65%;
  transform: scale(0);
  opacity: 0;
}

.icon3 {
  color: #F15A24;
  font-size: 50px;
  position: absolute;
  top: 18%;
  right: 52%;
  transform: scale(0);
  opacity: 0;
}

@media screen and (max-width: 800px) {
  header {
    height: 50vh;
  }

  header .banner {
    height: 50vh;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    padding-top: 3rem;
    padding-bottom: 3rem;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 0.98;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    z-index: 100;
  }

  .menu-icon.dark svg {
    color: #007f68;
    transition: 0.5s ease;
  }
  .menu-icon.light svg {
    color: #fff;
    transition: 0.5s ease;
  }

  .nav-link {
    color: #fff;
  }

  .icon1, .icon2, .icon3 {
    font-size: 25px;
  }
}

#about {
  padding: 5rem 2rem;
  background-color: #146666;
  display: flex;
  align-items: center;
  justify-content: center;
}

.picture {
  position: relative;
  width: 120px;
  height: 120px;
  padding: 0 3rem;
  opacity: 0;
}

.about-text {
  color: #fff;
  opacity: 0;
}


@media only screen and (max-width: 800px) {
  .about-text p {
    font-size: 12px;
    font-weight: 100;
  }

  .picture {
    padding: 0 1rem;
  }
}
.projects {
  height: 85vh;
  padding: 4rem;
  background: #2CB978;
}

.projects h1 {
  text-align: center;
  color: #fff;
}

.cards-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 3rem auto auto auto;
}

.cards-wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards-items {
  margin-bottom: 24px;
  opacity: 0;
}

.cards-item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.cards-item-link {
  display: flex;
  flex-flow: column;
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
  text-decoration: none;
}

.cards-item-pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.cards-item-pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #F7931E;
  box-sizing: border-box;
}

.cards-item-img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards-item-img:hover {
  transform: scale(1.1);
}

.cards-item-info {
  padding: 20px 30px 30px;
}

.cards-item-text {
  color: #007F68;
  font-size: 18px;
  line-height: 24px;
}


@media only screen and (min-width: 1024px) {
  .cards-items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards-item {
    margin-bottom: 2rem;
  }
  .projects {
    height: 100%;
    padding: 4rem 2rem 2rem 2rem;
  }

  .cards-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 90%;
    margin: 1rem auto auto auto;
  }
}

.skills {
  background-color: #ffffff;
  padding: 4rem;
  color: #172D51;
}

.skills h1 {
  text-align: center;
  color: #172D51;
}

.skills-container {
  padding: 4rem 6rem 0 4rem;
  max-width: 1120px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "titre1 css"
    "titre1 js"
    "titre2 php"
    "titre2 php";
  justify-content: center;
  align-items: center;
  text-align: center;
}

.h3-frontend {
  grid-area: titre1;
}

.h3-backend {
  grid-area: titre2;
}

.css {
  grid-area: css;
}

.js {
  grid-area: js;
}

.backend {
  /* padding-top: 2rem; */
  grid-area: php;
}

.skills-container ul {
  display: grid;
  grid-template-columns: repeat(3, 150px);
}

.skills-container li {
  display: flex;
  align-items: center;
}

.skills-container li svg {
  font-size: 40px;
  padding: 1rem;
}

@media screen and (max-width: 850px) {
  .skills {
    padding: 2rem 2rem 2rem 2rem;
  }
  .skills-container {
    padding: 4rem 1rem 0 1rem;
    max-width: 1120px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas:
      "titre1 css"
      "titre1 js"
      "titre2 php"
      "titre2 php";
  }

  .skills-container ul {
    display: flex;
    flex-wrap: wrap;
  }

  .skills-container li svg {
    font-size: 20px;
  }
}

footer {
  background-color: #007F68;
  padding-top: 4rem;
  padding-bottom: 4rem;
  color: #fff;
  font-size: 14px;
  text-align: center;
  position: relative;
}

.footer-networks {
  display: flex;
  justify-content: center;
}

.footer-networks li {
  margin: 0 5px;
}

.footer-networks li svg {
  font-size: 32px;
  color: #fff;
  transform: scale(1.0);
  transition: 0.3s ease-out;
}

.footer-networks li svg:hover {
  transform: scale(1.2);
  transition: 0.3s ease-out;
}

footer p {
    margin-top: 1rem;
}
